.App{
  background-color: #206649;
  width: 850px;
  min-height: 400px;
  border-radius: 8px;
  position: relative;
  user-select: none;
}

.TopArea{
  width: calc(100% - 50px);
  position: relative;
  height: 90px;
  display: flex;
  align-items: center;
  padding-inline: 25px;
  border-bottom: 2px white solid;
}

#PlayersHand{
  color: white;
  position: absolute;
  margin: 0px;
  padding: 0px;
  right: 25px;
}


.PlayersHand,.DealersHand{
  margin-top: 15px;
  width: calc(100% - 50px);
  padding: 25px;
  display: flex;
  flex-flow: row wrap;
  width: 50%;
  position: relative;
 
}

.MiddleArea{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.MiddleArea h2{
  width: 50%;
  text-align: center;
  color: white;
  margin: 0px;
  margin-top: 25px;
}

.DealersHand{
  border-left: 2px solid white;
}


.PlayersHand img,.DealersHand img{
  width: 100px;
  height: 150px;
}

.GameArea{
  width: 100%;
  display: flex;
  flex-direction: row;
}


.TopArea button,.EndGameScreen button{
  background-color: transparent;
  border: 2px white solid;
  color: white;
  width: 120px;
  margin-right: 20px;
  font-size: 20px;
  height: 50px;
  transition: .2s;
}
.TopArea button:hover,.EndGameScreen button:hover{
  transition: .2s;
  height: 60px;
  width: 130px;

}



.EndGameScreen{
  position: absolute;
  z-index: 1;
  background-color: rgb(0, 0, 0,.7);
  width: 100%;
  height: 100%;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 35px;
}
.EndGameScreen button{
  width: 250px;
}
.EndGameScreen button:hover{
  width: 260px;
  cursor: pointer;
}